.cards-item {
	border-radius: 10px;
	overflow: hidden;
	border: 2px solid rgba(3, 150, 79, 0.2);
	background-color: #f6f6f6;

	.cards-item__img {
		height: 250px;
		object-fit: cover;
		width: 100%;
	}

	.cards-item__text-container {
		align-items: center;
		padding: 0 10px 10px 10px;
	}
	.cards-item__name {
		text-align: center;
		font-size: 1.5rem;
		font-weight: 500;
		margin-bottom: 15px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	.cards-item__id {
		font-size: 1rem;
		font-weight: 400;
		margin-bottom: 5px;
	}
	.cards-item__gender {
		font-size: 1rem;
		font-weight: 400;
	}
}

.cards-item:hover {
	transform: scale(1);
	border: 2px solid #2684ff;
	box-shadow: 0 0px 15px rgba (0, 0, 0, 0.09);
}
